.App{
	text-align: center;
}

.App-logo{
	-ms-flex-align: left;
	    align-items: left;
	height: 80px;
}

.App-header{
	background-color: #333333;
	height: 80px;
	padding: 10px;
	margin: 0;
	color: #EEEEEE;
	grid-area: header;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: left;
	    align-items: left;
	-ms-flex-pack: left;
	    justify-content: left;
}

.App-title{
	font-size: 1.5em;
}

.App-intro{
	font-size: large;
}

ul.App-header li{
	display: inline;
	list-style-type: none;
	margin: 0;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}

ul.App-header li a{
	color: #EEEEEE;
	font-weight: bold;
	text-decoration: none;
	padding: 20px;
	display: inline-block;
}

ul.App-header li a.logo_home{
	padding: 0;
}



